async function (url, data) {
    let resData = null
    await fetch(url, {
        body: JSON.stringify(data),
        method: 'POST',
        headers: {
            Authorization: `Token ${config.token}`,
        }
    }).then(function (response) {
        resData = response.json()
    })
    return resData
}

async function (response) {
    let r = await response
    // console.log(r)
    return r.code === 0 ? r.data : null
}

